import react from 'react';
import {Card,CardBody,CardTitle,CardSubtitle,CardText,CardFooter,Button,Container,} from 'reactstrap';
import { Link } from "react-router-dom/cjs/react-router-dom.min";
//import clang from '../images/c.png';

const Proplist=({proplist})=>{
return(
    <Card className="text-center">
        <CardBody>

            <CardText>

            <p>Property Description: {proplist.description}</p>
            <p>Property Type: {proplist.type}</p>
            <p>Property Space: {proplist.area}</p>
            <p>Property Age: {proplist.age}</p>
            <p>Property Price: {proplist.price}</p>
            <p>Property Nego/Non-Negotiable: {proplist.nego}</p>
            <p>Property location: {proplist.location}</p>
            <p>Property Action: {proplist.action}</p>

            </CardText>


            {/* <Container className="text-center" >
                <Link to='/customers'>
                <button type="button" className="btn btn-info">Owner Details</button>
                </Link>
                <Button color="warning">Clear</Button>
            </Container> */}
        </CardBody>
    </Card>
) 
}
export default Proplist; 