import axios from "axios";
import {Fragment, React} from "react";
import { Link } from "react-router-dom";
import { useState } from "react/cjs/react.development";
import form, { Input, Label } from "reactstrap";
import base_url from '../api/bootapi';
import Contact from "./contact";
import Property from "./Properties";

{/* ------------------------------------form3--------------------------------- */}
const SearchBy=()=>{


    const [contact,setContact]= useState(
        {contact:0}
    );

    const [location,setLocation]= useState(
        {location:""}
    );

    const handlecontact=(e)=>{
        console.log(contact);
        getdatacontact(contact);
        e.preventDefault();

    };


    const getdatacontact=(data)=>{
        axios.get(`${base_url}/customers/${data.contact}`).then(
            (response)=>{
                console.log(response);
                setContact(response.data);
            },(error)=>{
                console.log(error);

            }
        )
    };

    const handlelocation=(e)=>{
        console.log(location);
        getdatalocation(location);
        e.preventDefault();

    };

    const getdatalocation=(data)=>{
        axios.get(`${base_url}/properties/${data.location}`).then(
            (response)=>{
                console.log(response);
                setLocation(response.data);
            },(error)=>{
                console.log(error);

            }
        )
    };

    return(
        <div>
    

<form onSubmit={handlecontact}>
        <Fragment>
            <h3 className="text-center my-3">
                Enter the Customer mobile number
            </h3>
            <Label for="contact" >Mobile No.</Label>
            <Input type="text" placeholder="Enter Mobile" name="contact" onChange={(e)=>{setContact({...contact,contact:e.target.value});
            }}> </Input>
            </Fragment>
            <button className="btn btn-outline-success" type="submit">Submit</button>
            <Link to="/info">
                <button type="button" className="btn btn-info">Add Customer</button>
                </Link> 
            
</form> 

                {contact.length > 0
                ? contact.map((item) => <Contact key={item.id} contact={item} />) 
                :"No properties"}


<form onSubmit={handlelocation}>
        <Fragment>
            <h3 className="text-center my-3">
                Enter the Property Location
            </h3>
            <Label for="location" >Location</Label>
            <Input type="text" placeholder="Enter location" name="location" onChange={(e)=>{setLocation({...location,location:e.target.value});
            }}> </Input>
            </Fragment>
            <button className="btn btn-outline-success" type="submit">Submit</button>
            <Link to="/info">
                <button type="button" className="btn btn-info">Add Property</button>
                </Link> 
            
</form>
                               

                {location.length > 0
                ? location.map((item) => <Property key={item.id} property={item} />) 
                :"No properties"}        
                
                     
                
    </div>
    )           
}
export default SearchBy;

