import react from 'react';
import {Card,CardBody,CardTitle,CardSubtitle,CardText,CardFooter,Button,Container, Row, Col,} from 'reactstrap';
import { Link } from "react-router-dom/cjs/react-router-dom.min";
//import { green } from '@material-ui/core/colors';
import Cust_info_OTM from './Cust_info_OTM';
import {useEffect,useState} from 'react';

const Course=({course})=>{

    return(
    <Card className="text-center">
        <CardBody>
            <Row style={{background:'#E0A800',rowspan:10}}>
            <Col><CardSubtitle className="font-weight-bold">
                Course Name: {course.name}    
            </CardSubtitle></Col>
            <Col><CardSubtitle className="font-weight-bold">Course Id: {course.id}</CardSubtitle></Col>
            </Row>
            <Row><CardText>
            {course.description}
            </CardText></Row>
            
            <CardText></CardText>
            
            <Row><Col><Card><CardBody>
                
                <Row>
                <CardText>
               <b>Duration:</b> {course.duration}
                </CardText>
                <CardText style={{color
                    :'#75BD43'}}>
                <b>Fee: </b>{course.fees}
                </CardText></Row>
                
                <Row><CardText>
                {/* <img src={course.image_link} alt="logo" /> */}
                </CardText>
                <CardText>
                
                </CardText>
                </Row></CardBody></Card></Col>
                
                <Col>
                <Card><CardBody>
                
                <Row>
               </Row>
                
                <Row>
                <CardText><b><h6>Syllabus</h6></b>
                <style>
                    {`#p-wrap {
                    white-space: pre-line;
                    }`}
                </style>
                <p id="p-wrap">{course.syllabus}</p>
                </CardText>
                <CardText></CardText>
                </Row></CardBody></Card>
                </Col>
                </Row>
                
                <CardText></CardText>
                
                {/* <Card><CardBody>
                
                </CardBody></Card> */}
                <CardText></CardText>
            <Container className="text-center" >
            <Link to="/info">
        <Button type="button" color="warning" className="btn btn-info"
        >Book Course</Button>
        </Link>
            </Container>
        </CardBody>
    </Card>
)
}
export default Course;