import React,{Component} from 'react';
import {  Container,CardFooter, Row, Col, Card, CardBody} from 'reactstrap';
import Headers from "./component/Header";
import "./App.css";
import "./cards/card-style.css";
import Menus from './component/Menus';
import Home from './component/Home';
import Cust_info from './component/Cust_info';
import {BrowserRouter as Router, Route } from "react-router-dom";
import Allcourses from './component/Allcourses';
import Address from './component/Address';
import C_Lang from './component/Cprog';
import C_Java from './component/C_Java';
import Adv_Java from './component/Adv_Java';
import ReactJs from './component/React';
import Cust_info_OTM from './component/Cust_info_OTM';
import SearchBy from './component/SearchBy';
import Webdevelopment from './component/Webdevelopment';


class App extends Component {
  
    render() {

        return (  
            
              <Router>
              <Container>
                  <Headers/>
                <Row>
                   {/* <Col ms={3}> */}
                      {/* <Card style={{width:'100%'}}>
                          <CardBody>
                      
                          <Menus/>
                      
                      </CardBody>
                      </Card> */}
                      <CardBody>
                          <Menus/>
                      </CardBody>
                </Row>
                    {/*</Col> */}
                   {/* <Col ms={8}> */}
                      <Row>
                      <Card style={{width:'100%'}}>
                      <CardBody>
                            
                          <Route path="/home" component={Home} exact/>
                          <Route path="/searchby" component={SearchBy} exact/>
                          <Route path="/customers" component={Allcourses} exact/>
                          {/* <Route path="/properties" component={Allproperties} exact/> */}
                          {/* <Route path="/courses/id" component={OneCourse} exact/> */}
                          <Route path="/aboutus" component={Home} exact/>
                          <Route path="/info" component={Cust_info_OTM} exact/>
                          <Route path="/customers/C programming" component={C_Lang} exact/>
                          <Route path="/customers/Core Java" component={C_Java} exact/>
                          <Route path="/customers/Advance Java" component={Adv_Java} exact/>
                          <Route path="/customers/React Js" component={ReactJs} exact/>
                          {/* <Route path="/properties/balaji nagar" component={balajinagar} exact/> */}
                          <Route path="/customers/Web Development" component={Webdevelopment} exact/>
                          {/* <Route path="/properties/dhankawadi" component={Dhankawadi} exact/>
                          <Route path="/properties/bibwewadi" component={Bibwewadi} exact/>
                          <Route path="/properties/padmawati" component={Padmawati} exact/>
                          <Route path="/properties/ambegaon" component={Ambegaon} exact/> */}
                          {/* <Route path="/contacts/contact" component={Contact} exact/> */}

                      </CardBody>
                      </Card>
                    
                  </Row>
                  
                  
                      
                   <CardFooter style={{backgroundColor:'#E9ECEF'}}>
                      <Row>
                      <Col> 
                        <Address/>
                      </Col>
                    </Row>
                  </CardFooter>
                
                  
                  
              </Container>
           
              </Router>      
              
        );
    }
}
 
export default App;