import react from 'react';
import {Card,CardBody,CardTitle,CardSubtitle,CardText,CardFooter,Button,Container,} from 'reactstrap';
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import Proplist from './Proplist';
//import clang from '../images/c.png';

const Contact=({contact})=>{

    const refreshPage=()=>{
        window.location.reload(false);
    };
    return(
    <Card className="text-center">
        <CardBody>

            <CardSubtitle className="font-weight-bold"> 
                {/* {cosole.log("Hello data",property.id)} */}
               <p>Property Id: {contact.id}</p>
              <p>Property Contact: {contact.name}</p>     
              <p>Property Contact: {contact.contact}</p>
              <p>Property Contact: {contact.mail}</p>

            </CardSubtitle>
            <CardText>
            {contact.proplist.length > 0
                ? contact.proplist.map((item) => <Proplist key={item.id} proplist={item} />) 
                :"No properties"}

            {/* <p>Property Description: {proplist.description}</p>
            <p>Property Type: {proplist.type}</p>
            <p>Property Space: {proplist.area}</p>
            <p>Property Age: {proplist.age}</p>
            <p>Property Price: {proplist.price}</p>
            <p>Property Nego/Non-Negotiable: {proplist.nego}</p>
            <p>Property location: {proplist.location}</p>
            <p>Property Action: {proplist.action}</p> */}

            </CardText>


            <Container className="text-center" >
                
                <button type="button" className="btn btn-info" onClick={refreshPage}>Clear</button>
                <Link to="/info">
                <button type="button" color="warning" className="btn btn-info">Add Customer</button>
                </Link> 
            </Container>
        </CardBody>
    </Card>
) 
}
export default Contact; 