import react from 'react';
import {Card,CardBody,CardTitle,CardSubtitle,CardText,CardFooter,Button,Container,} from 'reactstrap';
//import clang from '../images/c.png';
import { Link } from "react-router-dom/cjs/react-router-dom.min";

const Property=({property})=>{

    const refreshPage=()=>{
        window.location.reload(false);
    };

return(
    <Card className="text-center">
        <CardBody>

            <CardSubtitle className="font-weight-bold">
                {/* {cosole.log("Hello data",property.id)} */}
              <p>Property Id: {property.id}</p>
            
            </CardSubtitle>
            <CardText>
            
            {/* <p>Property Description: {property.description}</p>
            <p>Property Name: {property.name}</p>
            <p>Property Duration: {property.duration}</p>
            <p>Property Fees: {property.fees}</p> */}
            {/* <p>Course Syllabus: {course.syllabus}</p> */}
            {/* <p>Property Nego/Non-Negotiable: {property.nego}</p>
            <p>Property location: {property.location}</p>
            <p>Property Action: {property.action}</p> */}
            </CardText>

            <Container className="text-center" >
                <button type="button" className="btn btn-info" onClick={refreshPage}>Clear</button>
                <Link to="/searchby">
                <Button color="warning">Search Properties/Customer</Button>
                </Link>
            </Container>
        </CardBody>
    </Card>
)
}
export default Property; 