import React from 'react';
import { Card, CardBody, Row, Col } from 'reactstrap';
import img_grow from '../images/grow_atmos.jpg';
function Header({name,title}) {
    return(
        <div>
            
            
            
            <Card className=" bg-warning">
            <Row>
            <Col ms={4}>
            <img src={img_grow}/>
            </Col>
        
            <Col ms={8}> 
            <CardBody>
            <h1>Atmos Academy</h1>    
            <h6>Empowering Educational Enhancement</h6>    
            </CardBody>
            </Col>
            </Row>
            <Row><Col><b>Call +91 997 019 5959 </b></Col>
            {/* <Col ms={10}></Col> */}
            </Row>
            
            
            
            </Card>
            
        </div>
    );
}

export default Header;